.container {
  margin-top: 1rem;
}
button {
  outline: none;
  margin-bottom: 5px;
}
.duh.active {
  background-color: red;
}
li {
  list-style: none;
}
input {
  border: none;
  margin: auto;
  margin-top: 0.6rem;
  display: 100%;
  font-size: 1.3rem;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  width: 25px;
  height: 25px;
  outline: none;
}
br {
  display: block;
  margin: 1px 0;
}

.duh {
  margin: 0;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
  border: 1px solid black;
  padding-top: 0;
  padding-bottom: 0;
  height: auto !important;
  height: 20px;
  vertical-align: top;
}
.duh2 {
  margin: 0;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
  border: 1px solid black;
  padding-top: 0;
  padding-bottom: 0;
  height: auto !important;
  width: 30px;
  vertical-align: top;
  background-color: black;
  color: black;
}
.blah {
  margin: 0;
  padding: 0;
}
.number {
  font-size: 0.7rem;
  padding: 0;
  margin: 0;
  transform: translateY(-0.2rem);
  position: absolute;
}
